import Vue from 'vue';
//
import VueRouter from 'vue-router'
Vue.use(VueRouter);
//1.
import Index from '@/views/index'
import Home from '@/views/home'
import Board from '@/views/board'
import Login from '@/views/login'
import NotFound from '@/views/404'
import UpdatePassword from '@/views/update-password'
import pay from '@/views/pay'
import register from '@/views/register'
import center from '@/views/center'
import adminexam from '@/views/modules/exampaperlist/exam'
	import xuesheng from '@/views/modules/xuesheng/list'
	import psychologicaldata from '@/views/modules/psychologicaldata/list'
	import examquestion from '@/views/modules/examquestion/list'
	import exampaper from '@/views/modules/exampaper/list'
	import xinlizixunshi from '@/views/modules/xinlizixunshi/list'
	import config from '@/views/modules/config/list'
	import examrecord from '@/views/modules/examrecord/list'
	import discussxinlizixun from '@/views/modules/discussxinlizixun/list'
	import xinlizixun from '@/views/modules/xinlizixun/list'


//2.   
export const routes = [{
	path: '/',
	name: '',
	component: Index,
	children: [{
		// main
		path: '/',
		name: '',
		component: Home,
		meta: {icon:'', title:'center', affix: true}
	}, {
		path: '/updatePassword',
		name: '',
		component: UpdatePassword,
		meta: {icon:'', title:'updatePassword'}
	}, {
		path: '/pay',
		name: '',
		component: pay,
		meta: {icon:'', title:'pay'}
	}, {
		path: '/center',
		name: '',
		component: center,
		meta: {icon:'', title:'center'}
	}
	,{
		path: '/xuesheng',
		name: '',
		component: xuesheng
	}
	,{
		path: '/psychologicaldata',
		name: '',
		component: psychologicaldata
	}
	,{
		path: '/examquestion',
		name: '',
		component: examquestion
	}
	,{
		path: '/exampaper',
		name: '',
		component: exampaper
	}
	,{
		path: '/xinlizixunshi',
		name: '',
		component: xinlizixunshi
	}
	,{
		path: '/config',
		name: '',
		component: config
	}
	,{
		path: '/examrecord',
		name: '',
		component: examrecord
	}
	,{
		path: '/discussxinlizixun',
		name: '',
		component: discussxinlizixun
	}
	,{
		path: '/xinlizixun',
		name: '',
		component: xinlizixun
	}
	]
	},
	{
		path: '/adminexam',
		name: 'adminexam',
		component: adminexam,
		meta: {icon:'', title:'adminexam'}
	},
	{
		path: '/login',
		name: 'login',
		component: Login,
		meta: {icon:'', title:'login'}
	},
	{
		path: '/board',
		name: 'board',
		component: Board,
		meta: {icon:'', title:'board'}
	},
	{
		path: '/register',
		name: 'register',
		component: register,
		meta: {icon:'', title:'register'}
	},
	{
		path: '*',
		component: NotFound
	}
]
//3.VueRouter  
const router = new VueRouter({
	mode: 'hash',
	/*hashhistory*/
	routes //  routes: routes
})
const originalPush = VueRouter.prototype.push
//push
VueRouter.prototype.push = function push(location) {
	return originalPush.call(this, location).catch(err => err)
}
export default router;
