				<el-form-item class="input" v-if="type!='info'"  label="" prop="scoreofmentalhealthscale" >
					<el-input v-model.number="ruleForm.scoreofmentalhealthscale" placeholder="" clearable  :readonly="ro.scoreofmentalhealthscale"></el-input>
				</el-form-item>
