				</el-form-item>
				<el-form-item v-else class="input" label="SDS" prop="selfratingdepressionscalesdsscore" >
					<el-input v-model="ruleForm.selfratingdepressionscalesdsscore" placeholder="SDS" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="SAS" prop="selfratinganxietyscalesasscore" >
					<el-input v-model.number="ruleForm.selfratinganxietyscalesasscore" placeholder="SAS" clearable  :readonly="ro.selfratinganxietyscalesasscore"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="SAS" prop="selfratinganxietyscalesasscore" >
					<el-input v-model="ruleForm.selfratinganxietyscalesasscore" placeholder="SAS" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="scoreonthestressscale" >
					<el-input v-model.number="ruleForm.scoreonthestressscale" placeholder="" clearable  :readonly="ro.scoreonthestressscale"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="scoreonthestressscale" >
					<el-input v-model="ruleForm.scoreonthestressscale" placeholder="" readonly></el-input>
