				<el-form-item class="input" v-if="type!='info'"  label="" prop="emotionalstabilityscore" >
					<el-input v-model.number="ruleForm.emotionalstabilityscore" placeholder="" clearable  :readonly="ro.emotionalstabilityscore"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="emotionalstabilityscore" >
