				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="GPA" prop="gpagpa" >
					<el-input v-model.number="ruleForm.gpagpa" placeholder="GPA" clearable  :readonly="ro.gpagpa"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="GPA" prop="gpagpa" >
					<el-input v-model="ruleForm.gpagpa" placeholder="GPA" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="perceivedleveloflearningstress" >
					<el-input v-model="ruleForm.perceivedleveloflearningstress" placeholder="" clearable  :readonly="ro.perceivedleveloflearningstress"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="perceivedleveloflearningstress" >
					<el-input v-model="ruleForm.perceivedleveloflearningstress" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="socialactivitylevel" >
					<el-input v-model="ruleForm.socialactivitylevel" placeholder="" clearable  :readonly="ro.socialactivitylevel"></el-input>
				</el-form-item>
