					<el-input v-model="ruleForm.socialactivitylevel" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="dailysleepduration" >
					<el-input v-model="ruleForm.dailysleepduration" placeholder="" clearable  :readonly="ro.dailysleepduration"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="dailysleepduration" >
					<el-input v-model="ruleForm.dailysleepduration" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="regularityofdailyroutine" >
					<el-input v-model="ruleForm.regularityofdailyroutine" placeholder="" clearable  :readonly="ro.regularityofdailyroutine"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="regularityofdailyroutine" >
					<el-input v-model="ruleForm.regularityofdailyroutine" placeholder="" readonly></el-input>
				</el-form-item>
			</template>
			<el-form-item class="btn">
				<el-button class="btn3"  v-if="type!='info'" type="success" @click="onSubmit">
					<span class="icon iconfont icon-xihuan"></span>
					
				</el-button>
				<el-button class="btn4" v-if="type!='info'" type="success" @click="back()">
					<span class="icon iconfont icon-xihuan"></span>
					
				</el-button>
				<el-button class="btn5" v-if="type=='info'" type="success" @click="back()">
					<span class="icon iconfont icon-xihuan"></span>
					
				</el-button>
			</el-form-item>
		</el-form>
    

	</div>
</template>
<script>
	import { 
		isIntNumer,
	} from "@/utils/validate";
	export default {
		data() {
			var validateIntNumber = (rule, value, callback) => {
				if(!value){
					callback();
				} else if (!isIntNumer(value)) {
					callback(new Error(""));
				} else {
					callback();
				}
			};
			return {
				id: '',
				type: '',
			
			
				ro:{
					studentid : false,
					gender : false,
					age : false,
					gradelevel : false,
					professionalcategory : false,
					scoreofmentalhealthscale : false,
					selfratingdepressionscalesdsscore : false,
					selfratinganxietyscalesasscore : false,
					scoreonthestressscale : false,
					emotionalstabilityscore : false,
					gpagpa : false,
					perceivedleveloflearningstress : false,
					socialactivitylevel : false,
					dailysleepduration : false,
					regularityofdailyroutine : false,
				},
			
				ruleForm: {
					studentid: '',
					gender: '',
					age: '',
					gradelevel: '',
					professionalcategory: '',
					scoreofmentalhealthscale: '',
					selfratingdepressionscalesdsscore: '',
					selfratinganxietyscalesasscore: '',
					scoreonthestressscale: '',
					emotionalstabilityscore: '',
					gpagpa: '',
					perceivedleveloflearningstress: '',
					socialactivitylevel: '',
					dailysleepduration: '',
					regularityofdailyroutine: '',
				},
				genderOptions: [],

				rules: {
					studentid: [
						{ required: true, message: '', trigger: 'blur' },
					],
					gender: [
					],
					age: [
						{ validator: validateIntNumber, trigger: 'blur' },
					],
					gradelevel: [
					],
					professionalcategory: [
					],
					scoreofmentalhealthscale: [
						{ validator: validateIntNumber, trigger: 'blur' },
					],
					selfratingdepressionscalesdsscore: [
						{ validator: validateIntNumber, trigger: 'blur' },
					],
					selfratinganxietyscalesasscore: [
						{ validator: validateIntNumber, trigger: 'blur' },
					],
					scoreonthestressscale: [
						{ validator: validateIntNumber, trigger: 'blur' },
					],
					emotionalstabilityscore: [
						{ validator: validateIntNumber, trigger: 'blur' },
					],
					gpagpa: [
						{ validator: validateIntNumber, trigger: 'blur' },
					],
					perceivedleveloflearningstress: [
					],
					socialactivitylevel: [
					],
					dailysleepduration: [
					],
					regularityofdailyroutine: [
					],
				},
			};
		},
		props: ["parent"],
		computed: {



		},
		components: {
		},
		created() {
		},
		methods: {
			// 
			download(file){
				window.open(`${file}`)
			},
			// 
			init(id,type) {
				if (id) {
					this.id = id;
					this.type = type;
				}
				if(this.type=='info'||this.type=='else'||this.type=='msg'){
					this.info(id);
				}else if(this.type=='logistics'){
					for(let x in this.ro) {
						this.ro[x] = true
					}
					this.logistics=false;
					this.info(id);
				}else if(this.type=='cross'){
					var obj = this.$storage.getObj('crossObj');
					for (var o in obj){
						if(o=='studentid'){
							this.ruleForm.studentid = obj[o];
							this.ro.studentid = true;
							continue;
						}
						if(o=='gender'){
							this.ruleForm.gender = obj[o];
							this.ro.gender = true;
							continue;
						}
						if(o=='age'){
							this.ruleForm.age = obj[o];
							this.ro.age = true;
							continue;
						}
						if(o=='gradelevel'){
							this.ruleForm.gradelevel = obj[o];
							this.ro.gradelevel = true;
							continue;
						}
						if(o=='professionalcategory'){
							this.ruleForm.professionalcategory = obj[o];
							this.ro.professionalcategory = true;
							continue;
						}
						if(o=='scoreofmentalhealthscale'){
							this.ruleForm.scoreofmentalhealthscale = obj[o];
							this.ro.scoreofmentalhealthscale = true;
							continue;
						}
						if(o=='selfratingdepressionscalesdsscore'){
							this.ruleForm.selfratingdepressionscalesdsscore = obj[o];
							this.ro.selfratingdepressionscalesdsscore = true;
							continue;
						}
						if(o=='selfratinganxietyscalesasscore'){
							this.ruleForm.selfratinganxietyscalesasscore = obj[o];
							this.ro.selfratinganxietyscalesasscore = true;
							continue;
						}
						if(o=='scoreonthestressscale'){
							this.ruleForm.scoreonthestressscale = obj[o];
							this.ro.scoreonthestressscale = true;
							continue;
						}
						if(o=='emotionalstabilityscore'){
							this.ruleForm.emotionalstabilityscore = obj[o];
							this.ro.emotionalstabilityscore = true;
							continue;
						}
						if(o=='gpagpa'){
							this.ruleForm.gpagpa = obj[o];
							this.ro.gpagpa = true;
							continue;
						}
						if(o=='perceivedleveloflearningstress'){
							this.ruleForm.perceivedleveloflearningstress = obj[o];
							this.ro.perceivedleveloflearningstress = true;
							continue;
						}
						if(o=='socialactivitylevel'){
							this.ruleForm.socialactivitylevel = obj[o];
							this.ro.socialactivitylevel = true;
							continue;
						}
						if(o=='dailysleepduration'){
							this.ruleForm.dailysleepduration = obj[o];
							this.ro.dailysleepduration = true;
							continue;
						}
						if(o=='regularityofdailyroutine'){
							this.ruleForm.regularityofdailyroutine = obj[o];
							this.ro.regularityofdailyroutine = true;
							continue;
						}
					}
				}
				// 
				this.$http({
					url: `${this.$storage.get('sessionTable')}/session`,
					method: "get"
				}).then(({ data }) => {
					if (data && data.code === 0) {
						var json = data.data;
					} else {
						this.$message.error(data.msg);
					}
				});
				this.genderOptions = ",".split(',')
			
			},
			// 

			info(id) {
				this.$http({
					url: `psychologicaldata/info/${id}`,
					method: "get"
				}).then(({ data }) => {
					if (data && data.code === 0) {
						this.ruleForm = data.data;
						//
						let reg=new RegExp('../../../upload','g')//g
					} else {
						this.$message.error(data.msg);
					}
				});
			},

			// 
			async onSubmit() {
					var objcross = this.$storage.getObj('crossObj');
					if(!this.ruleForm.id) {
						delete this.ruleForm.userid
					}
					await this.$refs["ruleForm"].validate(async valid => {
						if (valid) {
							if(this.type=='cross'){
								var statusColumnName = this.$storage.get('statusColumnName');
								var statusColumnValue = this.$storage.get('statusColumnValue');
								if(statusColumnName!='') {
									var obj = this.$storage.getObj('crossObj');
									if(statusColumnName && !statusColumnName.startsWith("[")) {
										for (var o in obj){
											if(o==statusColumnName){
												obj[o] = statusColumnValue;
											}
										}
										var table = this.$storage.get('crossTable');
										await this.$http({
											url: `${table}/update`,
											method: "post",
											data: obj
										}).then(({ data }) => {});
									}
								}
							}
							
							await this.$http({
								url: `psychologicaldata/${!this.ruleForm.id ? "save" : "update"}`,
								method: "post",
								data: this.ruleForm
							}).then(async ({ data }) => {
								if (data && data.code === 0) {
									this.$message({
										message: "",
										type: "success",
										duration: 1500,
										onClose: () => {
											this.parent.showFlag = true;
											this.parent.addOrUpdateFlag = false;
											this.parent.psychologicaldataCrossAddOrUpdateFlag = false;
											this.parent.search();
											this.parent.contentStyleChange();
										}
									});
								} else {
									this.$message.error(data.msg);
								}
							});
						}
					});
			},
			// uuid
			getUUID () {
				return new Date().getTime();
			},
			// 
			back() {
				this.parent.showFlag = true;
				this.parent.addOrUpdateFlag = false;
				this.parent.psychologicaldataCrossAddOrUpdateFlag = false;
				this.parent.contentStyleChange();
			},
		}
	};
</script>
